:root {
  --light-text: hsl(0, 0%, 95%);
  --dark-texk: hsl(0, 0%, 10%);
  --alt-color: hsl(169, 42%, 49%);;
}

body {
  margin: 0;
  font-size: 1.1em;
  line-height: 2em;
  background-color: var(--light-text);
  color: var(--dark-texk);
}

.container {
  max-width: 1024px;
  margin: 0 auto;
}

.header {
  padding: 1em;
  background-color: var(--dark-texk);
  color: var(--alt-color);
  text-align: center;
}

.game-board {
  min-height: 128px;
  text-align: center;
  font-size: 1.5em;
}
.user-container {
  float: left;
  width: 50%;
}

.user-button {
  font-size: .9em;
  margin-right: 4px;
  padding:4px 8px;
  border-radius: 8px;
}

.computer-container {
  float: right;
  width: 50%;
}

.computer-choice { color: var(--alt-color) }

.result-container {
  text-align: center;
}

.result-hr { margin-bottom: 2em; }

.result-button {
  margin-right: 4px;
  padding:8px 8px;
  border: 1px solid var(--alt-color);
  border-radius: 8px;
  background-color: var(--dark-texk);
  color: var(--alt-color);
  font-size: 1.3em;
}



